body
{
    background-image: url('https://img.freepik.com/free-vector/futuristic-background-design_23-2148503793.jpg?size=626&ext=jpg&ga=GA1.1.2008272138.1721606400&semt=ais_user');
background-repeat: no-repeat;
background-size: cover;
}
.container{
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 70px;
    background: #fff;
    padding-bottom: 30px;
    width: 100%;
    
}
.header{
    display:flex;
    flex-direction: column;
    align-items: center;
    gap:9px;
width: 100%;
margin-top: 30px;
}
.text{
    color:#3c009d;
    font-size: 48px;
    font-weight: bold;
}
.underline{
    width: 61px;
    height: 6px;
    background: #3c009d;
    border-radius: 5px;
}
.inputs{
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    gap:25px;

}
.input{
    display: flex;
    align-items: center;
    margin:auto;
    width: 480px;
    height: 90px;
    gap:25px;
    background: #eaeaea;
    border-radius: 6px;
    font-size: 19px;
    font-weight: bold;

}
.icon{
    margin: 0px 30px;
    font-size: 30px;
}
.
.inputs input{
    height: 50px;
    width:400px;   
     background: transparent;
     border: none;
    outline: none;
    color:#797979;
    font-size:30px;
    font-weight: bold;
}
.forgot-password{
    padding-left: 62px;
    margin-top: 27px;
    color:#797979;
    font-size: 18px;
}
.forgot-password span{
    color: #4c00b4;
    cursor: pointer;
}
.submit-container{
    display: flex;
    gap:50px;
    margin:auto;
}
.submit{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background-color: #2c0ad6;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
    margin: auto;
}
.switch-action{
    font-size: 19px;
    color: rebeccapurple;
}
.switch-action span{
    color: red;
    font-size: bold;
    cursor: pointer;
}